<template>
  <div class="page" v-loading="loading">
    <el-collapse v-model="activeNames" @change="() =>{}" accordion>
      <el-collapse-item title="用户信息" name="1">
        <el-form>
          <el-form-item label="名称">
            <el-input readonly v-model="user.name" />
          </el-form-item>
          <el-form-item label="口令">
            <el-input type="password" v-model="user.password" />
          </el-form-item>
        </el-form>
      </el-collapse-item>
      <el-collapse-item title="首页" name="2">
        <el-form>
          <el-form-item>
            <textarea
              style="height: 40vh; width: 100%; word-wrap: break-word"
              type="textarea"
              v-model="user.index"
            ></textarea>
          </el-form-item>
        </el-form>
      </el-collapse-item>
      <el-collapse-item title="关于" name="3">
        <el-form>
          <el-form-item>
            <textarea
              style="height: 40vh; width: 100%; word-wrap: break-word"
              type="textarea"
              v-model="user.about"
            ></textarea>
          </el-form-item>
        </el-form>
      </el-collapse-item>
      <el-collapse-item title="备注" name="4">
        <el-form>
          <el-form-item>
            <el-input v-model="user.note" />
          </el-form-item>
        </el-form>
      </el-collapse-item>
    </el-collapse>
    <div class="btns">
      <wsh-button type="success" showComfirm text="修改" @comfirm="update" />
      <wsh-button type="info" showComfirm text="登出" @comfirm="logout" />
    </div>
  </div>
</template>

<script>
import { userGet, userUpdate } from '@/api';
import { PopTip } from '@/utils/tip';
import { store } from '@/store';
const tokenStore = store;

export default {
  name: 'User',
  data() {
    return {
      loading: false,
      activeNames: '1',
      user: {
        id: '',
        name: '',
        password: '*********',
        about: '',
        note: '',
        index: '',
      },
    };
  },
  mounted() {
    this.loading = true;
    userGet('admin')
      .then(res => {
        const { status, data } = res.data;
        if (status) {
          Object.assign(this.user, data);
        }
      })
      .finally(() => {
        this.loading = false;
      });
  },
  methods: {
    logout() {
      tokenStore.removeToken();
      location.reload();
    },
    update() {
      userUpdate(this.user).then(res => {
        const { status } = res.data;
        if (status) {
          PopTip.success('修改成功！');
        } else {
          PopTip.error('修改异常！');
        }
      });
    },
  },
};
</script>

<style scoped lang="scss">
.page {
  overflow: auto;
  @include scroll-style();
}
.btns {
  display: flex;
  justify-content: space-around;
  width: 100%;
  margin: 30px auto;
}
</style>
